import styled from "styled-components";
import { Element } from "react-scroll";

const StyledProjects = styled.div`
  width: 40%;
  display: flex;
  padding: 5%;
  align-items: flex-start;

  h2 {
      font-size: 3.4rem;
  }
  p {
      font-size: 1.6rem;
  }
`;

const Projects = () => (
  <Element name="projects">
    <StyledProjects>

    </StyledProjects>
  </Element>
);

export default Projects;